.transformacion {
  display: flex;
  padding: 0px 2rem 7rem 5rem;
  justify-content: space-between;
  background-image: url("../../img/bg-1.svg");
  background-repeat: no-repeat;
  background-position: right;
  flex-wrap: wrap;
  background-size: 99% 99%;
  background-position: 3500% 0%;
}
.transformacion-der,
.transformacion-izq {
  margin-top: 150px;
  width: 50%;
}
.transformacion-titulo {
  color: var(--color-principal);
  font-size: var(--principaltittle-size);
  font-weight: var(--tipo-medium);
}
.transformacion-boton {
  margin-top: 70px;
  width: 304px;
  height: 70px;
  background-color: var(--color-secundario);
  border-radius: 100px;
  color: white;
  font-size: 40px;
}
@media (max-width: 900px) {
  .transformacion-der,
  .transformacion-izq {
    margin-top: 80px;
    width: 50%;
  }
  .transformacion {
    position: relative;
    display: block;
    background-size: 30% 80%;
    background-position: 100% 0%;
  }
  .transformacion-boton {
    margin-top: 80px;
  }
  .transformacion-imagen {
    position: absolute;
    width: 50vw;
    right: 10px;
    top: 80px;
  }
}
@media (max-width: 700px) {
  .transformacion-der,
  .transformacion-izq {
    margin-top: 90px;
    width: 100%;
  }
  .transformacion {
    position: relative;
    display: block;
    background-size: 60% 90%;
    background-position: 100% -10%;
  }
  .transformacion-imagen {
    position: absolute;
    width: 90%;
    right: 5%;
    top: 28%;
  }
  .transformacion-boton {
    margin-top: 400px;
  }
}
