.trabajos {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
}
.trabajos-tarjeta {
  width: 32%;
  margin-bottom: 30px;
  border: solid 25px;
  border-image: linear-gradient(
      220deg,
      var(--color-secundario),
      var(--color-principal)
    )
    0.5;
  padding: 20px;
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.trabajos-tarjeta > h3 {
  text-align: center;
  font-size: 30px;
  color: var(--color-subtitle);
  margin: 20px;
  font-weight: var(--tipo-medium);
}
.trabajos-tarjeta-img {
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.trabajos-tarjeta-img > img {
  width: 100%;
}
.trabajos-tarjeta > a > button {
  color: white;
  width: 100%;
  padding: 20px;
  background-color: var(--color-principal);
  border-radius: 100px;
  transition: all 0.15s linear;
  font-size: 20px;
}

/* Estilos para los modals */

.modal {
  position: fixed;
  left: -100vw;
  top: 0;
  z-index: 9999999;
  background: var(--color-body);
  width: 100vw;
  height: 100vh;
  opacity: 0;
  transition: opacity 0.35s ease;
}
.modal .content-modal {
  width: 85vw;
  display: flex;
  align-items: center;
  position: fixed;
  height: 80vh;
  top: -100vh;
  transition: top 0.35s ease;
  margin-left: -15%;
  background: white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  border-radius: 100px;
  z-index: 99999;
}
.content-modal > p {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  padding-left: 200px;
  height: 100%;
  border-radius: 0px 100px 100px 0px;
  clip-path: polygon(20% 0, 100% 0, 100% 100%, 0% 100%);
  color: white;
  font-size: 28px;
}
.modal .content-modal img {
  margin-left: 50px;
  width: 30%;
}
.content-modal > p > a {
  margin: 20px;
  color: var(--color-body);
}
#modal-2 p {
  background-color: var(--color-secundario);
}
#modal-1 p,
#modal-3 p {
  background-color: var(--color-principal);
}

.modal:target {
  opacity: 1;
  left: 0;
}
.modal .btn-close-modal {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.modal:target .content-modal {
  top: 50px;
  left: 22%;
  transition: top 0.35s ease;
}
@media (max-width: 1200px) {
  .modal .content-modal {
    width: 100vw;
    margin-left: -22%;
  }
  .modal .content-modal p {
    padding-left: 12%;
  }
}
@media (max-width: 1000px) {
  .modal {
    padding: 0px;
  }
  .modal .content-modal {
    padding-top: 20px;
    border-radius: 0px;
    flex-direction: column;
    margin-left: -18%;
    height: 90vh;
    width: 90vw;
    margin-top: 0px;
  }
  .modal .content-modal img {
    padding: 40px;
    width: 100%;
  }
  .modal .content-modal p {
    border-top: 50px;
    padding-left: 40px;
    border-radius: 0px;
    clip-path: polygon(0% 12%, 100% 0, 100% 100%, 0% 100%);
  }
}

@media (max-width: 800px) {
  .modal .content-modal img {
    padding: 0px;
    margin-left: 10%;
    width: 80%;
  }
  .trabajos {
    display: block;
  }
  .trabajos-tarjeta {
    width: 100%;
    margin-left: 0px;
  }
  .trabajos-tarjeta > button {
    margin-left: 0vw;
    width: 100%;
  }
}
@media (max-width: 600px) {
  .modal .content-modal p {
    font-size: 16px;
    padding-left: 20px;
  }
  .modal .content-modal img {
    margin: 0px;
  }
}
