.contacto {
  margin: 10rem 2rem 5rem;
  height: auto;
}

.contacto-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.contacto-form {
  position: relative;
  width: 45%;
  height: 790px;
}

/* .contacto-form::after {
  content: " ";
  position: absolute;
  bottom: 5rem;
  left: 0;
  background-color: white;
  width: 100%;
  height: 5rem;
} */

.google-form {
  width: 100%;
  height: 100%;
}

.freebirdFormviewerViewItemsItemItem {
  height: 1px !important;
}

.freebirdFormviewerViewItemsTextTextItem {
  font-size: 10px;
}

.contacto-form h2 {
  color: var(--color-subtitle);
  font-weight: var(--tipo-light);
  font-size: 4rem;
  margin-bottom: 5rem;
}

.campo {
  margin-bottom: 1.5rem;
  width: 100%;
}

.campo label {
  display: block;
  width: 100%;
  color: var(--color-secundario);
  font-size: var(--buttons-size);
}

.campo input {
  width: 100%;
  height: 3.5rem;
  background-color: #7979792d;
  font-size: var(--buttons-size);
  padding-left: 2.25rem;
}

.campo input::placeholder {
  /* color: rgba(0, 0, 0, 0.6); */
  color: #01538a7a;
}

.contacto-form button {
  height: 3.8rem;
  width: 15rem;
  background-color: var(--color-principal);
  align-self: center;
  border-radius: 15px;
  font-size: var(--buttons-size);
  font-weight: var(--tipo-regular);
  color: var(--color-auxiliar);
}

.contacto-img {
  position: relative;
  width: 55%;
  height: 80%;
}

.contacto-escena {
  width: 83%;
  margin-top: 3.2rem;
  margin-left: 4.5rem;
}

.contacto-fondo {
  position: absolute;
  top: -6rem;
  right: -0;
  width: 90%;
  margin-right: -2rem;
  z-index: -10;
}

.h2-mobile {
  display: none;
}

.p-mobile {
  display: none;
}

@media (max-width: 800px) {
  .contacto {
    margin: 4rem 2rem 5rem;
    height: auto;
  }

  .h2-mobile {
    display: block;
    color: var(--color-subtitle);
    font-weight: var(--tipo-light);
    font-size: 4rem;
    margin-bottom: 1rem;
    text-align: center;
  }

  .p-mobile {
    display: block;
    color: var(--color-subtitle);
    font-weight: var(--tipo-light);
    font-size: 3rem;
    margin-bottom: 5rem;
    text-align: center;
  }

  .contacto-container {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: flex-start;
  }

  .contacto-img {
    position: relative;
    width: 100%;
    margin-bottom: 5rem;
  }

  .contacto-fondo {
    position: absolute;
    top: -4rem;
    right: -0;
    width: 80%;
    margin-right: -2rem;
    z-index: -10;
  }

  .contacto-form {
    margin: -1.1rem;
    width: 96vw;
    height: 632px;
    overflow-y: hidden;
  }

  .google-form {
    height: 1000px;
  }

  .contacto-form form {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .contacto-form h2 {
    display: none;
  }

  .campo label {
    font-size: 2.5rem;
  }
  .campo input {
    font-size: 2.5rem;
    height: 6.5rem;
  }

  .contacto-form button {
    font-size: 2.5rem;
    height: 6.5rem;
    width: 24rem;
  }
}

@media (max-height: 640px) {
  .contacto-form {
    height: 675px;
  }
}
