.footer {
  height: 17.8rem;
  width: 100%;
  background-image: url("../../img/footer.svg");
  background-size: 112%;
  background-repeat: no-repeat;
  font-size: 1.25rem;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  color: var(--color-auxiliar);
  height: 100%;
  padding: 0 9.5rem 0 9rem;
}

.footer-correo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-correo img {
  width: 4.5rem;
  margin-bottom: 1.25rem;
}

.footer-logo {
  width: 14rem;
  margin-right: 1rem;
  margin-bottom: 0.8rem;
}

.footer-telefono {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-telefono img {
  width: 4rem;
  margin-bottom: 1.25rem;
}

@media (max-width: 960px) {
  .footer {
    background-size: 180%;
  }
}

@media (max-width: 601px) {
  .footer-container {
    padding: 0;
  }
  .footer {
    height: 24rem;
    background-image: url("../../img/footer-movil.svg");
    background-repeat: repeat-x;
    background-size: 110%;
    background-position: -1.5rem;
  }

  .footer-container {
    position: relative;
    justify-content: space-between;
    margin: 0 2rem;
  }

  .footer-correo {
    order: 1;
    width: 50%;
    align-self: flex-start;
    margin-top: 2rem;
    font-size: 1.65rem;
  }

  .footer-correo img {
    width: 4.5rem;
    margin-bottom: 2rem;
  }

  .footer-logo {
    order: 3;
    margin: auto;
    margin-bottom: 2rem;
    align-self: center;
    width: 14rem;
  }

  .footer-telefono {
    width: 50%;
    order: 2;
    align-self: flex-start;
    margin-top: 2rem;
    font-size: 1.65rem;
  }

  .footer-telefono img {
    width: 4.5rem;
  }
}

@media (max-width: 321px) {
  .footer {
    background-size: 122%;
  }
}
