.plan-accion {
  padding: 0 2rem;
  margin: 5rem 0;
}

.plan-accion-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.plan-accion-img {
  position: relative;
  width: 45%;
}

.fondo-4 {
  width: 110%;
}

.escena-4 {
  position: absolute;

  width: 100%;
  top: 20%;
  left: 25%;
  width: 105%;
  top: 6%;
  left: 9%;

  z-index: 100;
}

.plan-accion-txt {
  display: flex;

  width: 55%;
  width: 50%;

  flex-direction: column;
}

.plan-accion-txt h2 {
  font-size: var(--subtittle-size);
  font-family: var(--tipografia);
  font-weight: var(--tipo-light);
  color: var(--color-subtitle);
  text-align: right;
  margin: 3rem 0 4rem;
}

.plan-accion-txt button {
  height: 4.5rem;
  width: 18rem;
  background-color: var(--color-secundario);
  color: var(--color-auxiliar);
  align-self: center;
  border-radius: 60px;
  font-size: var(--buttons-size);
  margin-left: 4rem;
}

@media (max-width: 800px) {
  .plan-accion {
    margin: 2rem 0;
  }

  .plan-accion-img {
    width: 100%;
    margin-bottom: 2rem;
  }
  .fondo-4 {
    width: 100%;
  }
  .escena-4 {
    width: 90%;
    top: 10%;
    left: 5%;
  }
  .plan-accion-txt {
    width: 100%;
  }
  .plan-accion-txt h2 {
    text-align: center;
    margin-bottom: 0;
  }
  .plan-accion-txt button {
    display: none;
  }
}
