/* aqui colocaremos los estilos generales, podemos dejarlo como index.css o si quieres lo llamamos app.css ya que es el componente padre de todos */
/* importo todos los css, asi podran heredar desde aqui las variables */
@import "./components/index.css";

/* Variables */
:root {
  /* colores */
  --color-principal: #5700ad;
  --color-secundario: #01548a;
  --color-body: #e5e5e5;
  --color-auxiliar: #ffffff;
  --color-subtitle: #797979;

  /* tipografias */
  --tipografia: "Roboto";
  --tipo-medium: 500;
  --tipo-regular: 400;
  --tipo-light: 300;

  /* tamaño de letra */
  --tittle-size: 6rem;
  --subtittle-size: 4rem;
  --principaltittle-size: 5rem;
  --projectcard-size: 2rem;
  --servicecard-size: 2rem;
  --buttons-size: 1.35rem; /* Se puede usar en contact y form layer */
  --menu-size: 1rem;
}

* {
  padding: 0;
  margin: 0;
  border-style: none;
  box-sizing: border-box;
  font-family: var(--tipografia);
}

button {
  cursor: pointer;
}

@media (max-width: 2000px) {
  html {
    font-size: 22px;
  }
}

@media (max-width: 1800px) {
  html {
    font-size: 20px;
  }
}

@media (max-width: 1600px) {
  html {
    font-size: 18px;
  }
}

@media (max-width: 1400px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 14px;
  }
}
@media (max-width: 1050px) {
  html {
    font-size: 12px;
  }
}
@media (max-width: 900px) {
  html {
    font-size: 10px;
  }
}
@media (max-width: 600px) {
  html {
    font-size: 8px;
  }
}
