.proyectos {
  background-image: url("../../img/bg-3.svg");
  background-repeat: no-repeat;
  background-position: right;
  display: flex;
  padding: 50px;
  margin-bottom: 10vh;
  background-size: 80% 85%;
  background-position: 200% 40%;
}
.proyectos-izq {
  width: 50vw;
}
.proyectos-der {
  width: 30vw;
}
.proyectos-izq > h2 {
  text-align: left;
  color: var(--color-subtitle);
  font-size: var(--subtittle-size);
  font-weight: var(--tipo-light);
}
.proyectos-izq > button {
  margin-top: 70px;
  padding: 20px;
  background-color: var(--color-secundario);
  border-radius: 100px;
  color: white;
  font-size: 30px;
}
.proyectos-der {
  margin-top: 40px;
}
.proyectos-der > img {
  width: 50vw;
  margin-left: -20%;
}
