@media (max-width: 950px) {
  .proyectos {
    margin-top: 50px;
    position: relative;
    display: block;
    width: 100%;
    background-size: 80% 50%;
    background-position: 100% 0%;
  }
  .proyectos-izq {
    margin-top: 400px;
    width: 100%;
  }
  .proyectos-izq > h2 {
    width: 100%;
    text-align: center;
  }
  .proyectos-izq > button {
    width: 100%;
  }
  .proyectos-der > img {
    position: absolute;
    width: 80vw;
    left: 30%;
    top: 5%;
  }
}
@media (max-width: 600px) {
  .proyectos {
    margin-top: 0px;
  }
  .proyectos-der > img {
    top: 10%;
  }
  .proyectos-izq {
    margin-top: 320px;
  }
}
@media (max-width: 450px) {
  .proyectos {
    margin-top: 0px;
    margin-bottom: 0px;
    background-size: 80% 50%;
    background-position: 100% 8%;
  }
  .proyectos-izq {
    padding-top: 0px;
  }
  .proyectos-der > img {
    top: 18%;
    left: 33%;
  }
  .proyectos-izq {
    margin-top: 280px;
  }
  .proyectos-izq > button {
    margin-top: 30px;
    margin-bottom: -60px;
  }
}
