.necesidades {
  display: flex;
  padding-bottom: 2vh;
}
.necesidades-izq {
  position: relative;
  width: 50vw;
  padding: 2rem;
}
.necesidades-fondo {
  position: absolute;
  width: 65%;
  top: -17%;
  left: 16%;
  z-index: 100;
}
.necesidades-img {
  position: absolute;
  width: 70%;
  top: 7%;
  left: 25%;
  z-index: 100;
}
.necesidades-der {
  width: 50vw;
  padding: 50px;
}
.necesidades-der > h2 {
  text-align: right;
  color: var(--color-subtitle);
  font-size: var(--subtittle-size);
  font-weight: var(--tipo-light);
}
@media (max-width: 1000px) {
  .necesidades-fondo {
    top: -12%;
    left: 16%;
    width: 85%;
  }
  .necesidades-img {
    width: 90%;
    top: 10%;
    left: 25%;
  }
}
@media (max-width: 800px) {
  .necesidades {
    display: block;
  }
  .necesidades-izq,
  .necesidades-der {
    width: 100%;
  }
  .necesidades-fondo {
    position: absolute;
    width: 70%;
    top: -300%;
    left: 5%;
    z-index: 100;
  }
  .necesidades-img {
    position: absolute;
    width: 78%;
    top: -140%;
    left: 20%;
    z-index: 100;
  }
  .necesidades-der {
    padding-top: 0px;
    padding: 20px;
    margin-top: 400px;
  }
  .necesidades-der > h2 {
    text-align: center;
  }
}
@media (max-width: 600px) {
  .necesidades-der {
    margin-top: 300px;
  }
}
@media (max-width: 450px) {
  .necesidades-der {
    margin-top: 140px;
    margin-bottom: -100px;
  }
}
