.planes-geinsoft {
  margin: 5rem 0;
  padding: 2rem;
  display: flex;
  align-items: center;
  position: relative;
}

.planes-fondo {
  position: absolute;
  width: 100%;
  left: 0;
  z-index: -10;
}

.planes-geinsoft-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.plan-container {
  position: relative;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 17rem;
  font-family: var(--tipografia);
  font-size: var(--tipo-regular);
  text-align: center;
  box-shadow: 0 0 2.5px 0.5px var(--color-secundario);
  border-radius: 30px;
  height: 30rem;
  background-color: var(--color-auxiliar);
}

.plan-img-circulo {
  width: 13rem;
  height: auto;
}

.plan-img {
  width: 100%;
  height: auto;
}

.ecommerce {
  margin-top: -14.5rem;
  width: 8.5rem;
}

.cms {
  margin-top: -13.5rem;
  margin-left: 0.5rem;
  width: 8.5rem;
}

.soft-medida {
  margin-top: -13.5rem;
  width: 7.5rem;
}

.multi-plataforma {
  margin-top: -13.5rem;
  width: 9rem;
}

.plan-comunes {
  display: flex;
  height: 14rem;
  padding: 1rem 0;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 30px;
}

.plan-ecommerce {
  background-color: var(--color-secundario);
}

.plan-ecommerce-container {
  box-shadow: 0 0 2.5px 0.5px var(--color-secundario);
}

.plan-ecommerce .button {
  background-color: var(--color-secundario);
}

.plan-ecommerce .button a {
  color: var(--color-secundario);
}

.plan-cms {
  background-color: var(--color-principal);
}

.plan-cms-container {
  box-shadow: 0 0 2.5px 0.5px var(--color-principal);
}

.plan-cms .button {
  background-color: var(--color-principal);
  color: var(--color-principal);
}

.plan-cms .button a {
  color: var(--color-principal);
}

.plan-soft-medida {
  background-color: var(--color-secundario);
}

.plan-soft-medida-container {
  box-shadow: 0 0 2.5px 0.5px var(--color-secundario);
}

.plan-soft-medida .button {
  background-color: var(--color-secundario);
  color: var(--color-secundario);
}

.plan-soft-medida .button a {
  color: var(--color-secundario);
}

.plan-multi-plataforma {
  background-color: var(--color-principal);
}

.plan-multi-plataforma-container {
  box-shadow: 0 0 2.5px 0.5px var(--color-principal);
}

.plan-multi-plataforma .button {
  background-color: var(--color-principal);
  color: var(--color-principal);
}

.plan-multi-plataforma .button a {
  color: var(--color-principal);
}

.plan-comunes h3 {
  color: var(--color-auxiliar);
  font-family: var(--tipografia);
  font-size: 1.5rem;
  padding: 0 1.5rem;
  font-weight: var(--tipo-regular);
}

.plan-comunes img {
  height: 1.15rem;
  margin-bottom: 0.25rem;
}

.plan-comunes .button {
  height: 3.8rem;
  width: 12rem;
  padding: 0 1rem;
  background-color: var(--color-auxiliar);
  align-self: center;
  border-radius: 60px;
  font-size: var(--buttons-size);
  font-weight: var(--tipo-medium);
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.button a {
  text-decoration: none;
}

@media (max-width: 800px) {
  .planes-fondo {
    display: none;
  }

  .plan-container {
    width: 48%;
    height: 48.5rem;
    margin-bottom: 3.5rem;
  }

  .plan-img-circulo {
    width: 20rem;
    height: auto;
  }

  .plan-comunes {
    height: 22rem;
    padding: 4rem 0 2rem;
  }

  .plan-comunes img {
    height: 2rem;
    margin-bottom: 0.5rem;
  }

  .ecommerce {
    margin-top: -25rem;
    width: 11rem;
  }

  .cms {
    margin-top: -25rem;
    margin-left: 0.5rem;
    width: 11rem;
  }

  .soft-medida {
    margin-top: -24rem;
    width: 11rem;
  }

  .multi-plataforma {
    margin-top: -24rem;
    width: 12rem;
  }

  .plan-comunes h3 {
    font-size: 2.5rem;
    width: 90%;
  }

  .plan-comunes .button {
    height: 5.5rem;
    width: 20rem;
    font-size: 2rem;
  }
}

/* TODO estilos para las tarjetas. Entre 599 y 441  */

@media (max-width: 600px) {
  .planes-geinsoft-container {
    flex-direction: column;
    align-items: center;
    width:100%;
    justify-items: center;
  }

  .plan-container {
    width: 80%;
    height: 60.5rem;
    margin-bottom: 3.5rem;
  }

  .plan-comunes {
    height: 30.5rem;
  }

  .plan-img-circulo {
    width: 26rem;
    height: auto;
  }

  .plan-comunes img {
    height: 2rem;
    margin-bottom: 0.5rem;
  }

  .ecommerce {
    margin-top: -25rem;
    width: 11rem;
  }

  .cms {
    margin-top: -25rem;
    margin-left: 0.5rem;
    width: 11rem;
  }

  .soft-medida {
    margin-top: -24rem;
    width: 11rem;
  }

  .multi-plataforma {
    margin-top: -24rem;
    width: 12rem;
  }

  .plan-comunes h3 {
    font-size: 3.5rem;
    width: 90%;
  }

  .plan-comunes img {
    height: 3rem;
  }

  .plan-comunes .button {
    height: 7rem;
    width: 24rem;
    padding: 0 1rem;
    background-color: var(--color-auxiliar);
    align-self: center;
    border-radius: 60px;
    font-size: 2.5rem;
    font-weight: 700;
  }

}

@media (max-width: 440px) {
  .planes-fondo {
    display: none;
  }

  .plan-comunes {
    height: 25rem;
    padding: 2rem 0 2rem;
  }

  .plan-comunes h3 {
    font-size: 2.5rem;
    width: 90%;
}

  .plan-comunes img {
    height: 3rem;
    margin-bottom: 0.5rem;
  }

  .plan-comunes .button {
    height: 5.5rem;
    width: 20rem;
    font-size: 2rem;
    font-weight: 400;
}

.plan-img-circulo {
  width: 20rem;
  height: auto;
}
  .planes-geinsoft-container {
    flex-direction: column;
    align-items: center;
    width: 100%;
    justify-items: center;
  }

  .ecommerce {
    margin-top: -22rem;
    width: 11rem;
}

.cms {
  margin-top: -21rem;
  margin-left: 0.5rem;
  width: 11rem;
}

.soft-medida {
  margin-top: -21rem;
  width: 11rem;
}

.multi-plataforma {
  margin-top: -22rem;
  width: 12rem;
}

  .plan-container {
    width: 80%;
    height: 48.5rem;
    margin-bottom: 3.5rem;
  }
}
